export default {
	data() {
		return {
			username:"",
			password:"",
			openId:'',
		};
	},
	onLoad(){
		this.openId = this.getCookie("openId");
	},
	methods:{
		getCookie(cname){
			var name = cname + "=";
			var ca = document.cookie.split(';');
			for(var i=0; i<ca.length; i++) 
			{
			var c = ca[i].trim();
			if (c.indexOf(name)==0) return c.substring(name.length,c.length);
			}
			return "";
		},
		getinfo(){
			if(this.username == ''){
				uni.showToast({
				icon: 'none',
				title: '请输入账号',
				duration: 2000
				});
			}else if(this.password == ''){
				uni.showToast({
				icon: 'none',
				title: '请输入密码',
				duration: 2000
				});
			}else{
				uni.request({
					url: this.$api+'/weixin/shishenxin/login', 
					data: this.getmd5({
						userName:this.username,
						passWord:this.password,
						openId:this.openId,
					}),
					header: {
						'content-type': 'application/json'
					},
					method: "POST",
					success: (res) => {
						if (res.data.code != 200) {
							uni.showToast({
							icon: 'none',
							title: res.data.msg,
							duration: 2000
							});
						} else {
							uni.hideLoading();
							var data = res.data.data;
							uni.setStorageSync('openId', data.openId);
							uni.reLaunch({
								url:'../index/index'
							})
						}
					},
					fail: () => {
						uni.hideLoading();
						uni.showToast({
						icon: 'none',
						title: '获取失败',
						duration: 2000
						});
					}
				});
			}
			
		},
	}
}
